.menu.cast-list {
  margin-top: 2em;
  text-align: center;
}

.character {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: contain;
  min-width: 150px;
  min-height: 140px;
  position: relative;
  margin-bottom: 4em;
  opacity: 0.3;
}

.character.present {
  opacity: 1;
}

.character.dead {
  opacity: 1;
  -webkit-filter: sepia(100%);
          filter: sepia(100%);
}

.character a {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.character span {
  color: #FFFFFF;
  position: absolute;
  bottom: -1.5em;
  width: 100%;
  left: 0;
  right: 0;
}

.character a:hover span {
  color: greenyellow;
}

@media (max-width: 500px) {
  .character {
    display: none;
  }
  .character.present {
    display: block;
  }
}



