aside {
  background-color: #0f3647;
  border: 2px solid #FFFFFF;
  width: 18em;
  min-width: 250px;
  float: right;
  padding: 1em;
  margin-left: 1em;
  margin-top: 1em;
  margin-right: 10em;
}

h4 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0.5em;
  font-size: 1.5em;
  font-weight: normal;
}

.npc {
  background-position: center left;
  background-repeat: no-repeat;
  background-size: 70px 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.75em;
  padding-left: 80px;
  height: 80px;
}

.npc p {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0;
  color: #FFFFFF;
  font-family: "cronos-pro-display", sans-serif;
  font-size: 1.1em;
}

@media (max-width: 500px) {
  aside {
    width: 90%;
    min-width: 90%;
    float: none;
    clear: both;
    border-left: 0;
    border-right: 0;
    margin: 1em auto;
    padding: 1em;
  }

  .npc {
    width: 50%;
    margin: 0 auto;
  }
}



